<template>
  <div class="messanger__window-footer">
    <textarea name="" placeholder="Написать сообщение..." v-model="message"></textarea>
    <button class="footer-btn__pohoto">
      <img src="../assets/images/photo.svg" alt="">
    </button>
    <button class="footer-btn__send" @click="sendMessage">
      <img src="../assets/images/send.svg" alt="">
    </button>
  </div>
</template>

<script>
export default {
  name: "footer",
  props: {
    sendId: Number
  },
  data(){
    return{
      message: '',
    }
  },
  methods: {
    sendMessage(){
      const time = `${new Date().getHours()}:${new Date().getMinutes()}`;
      if(this.message.length > 0){
        const mess = {}
        mess.body = this.message.trim();
        mess.sendId = this.sendId;
        mess.time = time;
        this.$emit('new-message', mess)
        this.message = ''
      }
    }
  }
  
};
</script>