<template>
  <div class="messanger__window-content main">
    <div class="main-item" v-for="(mess, index) in messages" :key="index">
      <div class="main-item__body" :class="{
        send: mess.sendId == sendId, 
        get: mess.sendId != sendId, 
        }">
        <div class="main-item__text">
          <p>{{mess.body}}</p>
        </div>
        <div class="main-item__time">{{mess.time}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "content",
  props: {
    messages: Array,
    sendId: Number,
  }
};
</script>